.all-creators-page {
  padding: var(--spacing-8);
  max-width: 1600px;
  margin: 0 auto;
}

.creator-avatar-large {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  object-fit: cover;
}

.creator-name {
  font-weight: var(--font-medium);
  color: var(--text-primary);
}

.creator-handle {
  color: var(--text-secondary);
  font-size: var(--text-sm);
}

.platform-badge {
  display: inline-block;
  padding: var(--spacing-1) var(--spacing-3);
  background: var(--surface-hover);
  border-radius: var(--radius-md);
  font-size: var(--text-xs);
  font-weight: var(--font-medium);
  text-transform: capitalize;
}

.platform-badge.tiktok { background: #FEE2E2; color: #991B1B; }
.platform-badge.instagram { background: #FCE7F3; color: #831843; }
.platform-badge.youtube { background: #FEE2E2; color: #991B1B; }
.platform-badge.facebook { background: #DBEAFE; color: #1E3A8A; }
.platform-badge.snapchat { background: #FEF3C7; color: #78350F; }

@media (max-width: 768px) {
  .all-creators-page {
    padding: var(--spacing-4);
  }

  .page-header {
    flex-direction: column;
    gap: var(--spacing-3);
  }

  .header-actions {
    width: 100%;
  }
}

