.app {
  display: flex;
  height: 100vh;
  overflow: hidden;
  background-color: var(--background);
}

.dashboard-container {
  display: flex;
  height: 100vh;
  width: 100%;
  overflow: hidden;
}

.main-content {
  flex: 1;
  height: 100vh;
  overflow-y: auto;
  overflow-x: hidden;
}

/* Mobile: Adjust for bottom navigation */
@media (max-width: 768px) {
  .app {
    flex-direction: column;
  }
  
  .main-content {
    padding-bottom: 80px; /* Space for bottom nav */
  }
}
