.client-dashboard {
  padding: var(--spacing-10);
  max-width: 1600px;
  margin: 0 auto;
}

/* Header */
.dashboard-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--spacing-8);
  flex-wrap: wrap;
  gap: var(--spacing-4);
}

.header-title h1 {
  font-size: var(--text-2xl);
  font-weight: var(--font-bold);
  color: var(--text-primary);
  margin: 0;
}

.header-filters {
  display: flex;
  gap: var(--spacing-3);
  align-items: center;
  flex-wrap: wrap;
}

.campaign-select {
  padding: var(--spacing-2) var(--spacing-4);
  border: 1px solid var(--border);
  border-radius: var(--radius-lg);
  background-color: var(--surface);
  color: var(--text-primary);
  font-size: var(--text-sm);
  font-weight: var(--font-medium);
  cursor: pointer;
  transition: all var(--transition-fast);
}

.campaign-select:hover {
  border-color: var(--text-secondary);
}

/* Metrics Grid */
.metrics-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: var(--spacing-6);
  margin-bottom: var(--spacing-10);
}

.metric-card {
  background-color: var(--surface);
  border: 1px solid var(--border);
  border-radius: var(--radius-lg);
  padding: var(--spacing-6);
  position: relative;
  transition: all var(--transition-fast);
}

.metric-card:hover {
  box-shadow: var(--shadow-md);
  transform: translateY(-2px);
}

.metric-header {
  display: flex;
  align-items: center;
  gap: var(--spacing-2);
  margin-bottom: var(--spacing-4);
}

.metric-icon {
  font-size: var(--text-lg);
}

.metric-label {
  font-size: var(--text-sm);
  color: var(--text-secondary);
  font-weight: var(--font-medium);
}

.metric-value {
  font-size: var(--text-4xl);
  font-weight: var(--font-bold);
  color: var(--text-primary);
  margin-bottom: var(--spacing-2);
  line-height: 1.2;
}

.metric-badge {
  position: absolute;
  top: var(--spacing-4);
  right: var(--spacing-4);
  width: 24px;
  height: 24px;
  border-radius: var(--radius-full);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  font-weight: var(--font-semibold);
}

.metric-badge.green {
  background-color: #D1FAE5;
  color: #059669;
}

.metric-badge.blue {
  background-color: #DBEAFE;
  color: #2563EB;
}

.metric-badge.purple {
  background-color: #E0E7FF;
  color: #4F46E5;
}

.metric-badge.orange {
  background-color: #FEF3C7;
  color: #F59E0B;
}

.change-indicator {
  font-size: var(--text-sm);
  font-weight: var(--font-medium);
}

.change-indicator.positive {
  color: var(--success);
}

.change-indicator.negative {
  color: var(--error);
}

/* Card Sections */
.card {
  background-color: var(--surface);
  border: 1px solid var(--border);
  border-radius: var(--radius-lg);
  padding: var(--spacing-8);
  margin-bottom: var(--spacing-8);
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--spacing-6);
}

.section-header h2 {
  font-size: var(--text-xl);
  font-weight: var(--font-bold);
  color: var(--text-primary);
  margin: 0;
}

.metrics-btn,
.view-all-btn {
  padding: var(--spacing-2) var(--spacing-4);
  border: 1px solid var(--border);
  border-radius: var(--radius-md);
  background-color: var(--surface);
  color: var(--text-secondary);
  font-size: var(--text-sm);
  font-weight: var(--font-medium);
  cursor: pointer;
  transition: all var(--transition-fast);
}

.metrics-btn:hover,
.view-all-btn:hover {
  background-color: var(--surface-hover);
  color: var(--primary);
  border-color: var(--primary);
}

/* Chart Section */
.chart-section {
  min-height: 400px;
}

/* Videos Table */
.videos-table {
  overflow-x: auto;
}

.videos-table table {
  width: 100%;
  border-collapse: collapse;
}

.videos-table thead th {
  text-align: left;
  padding: var(--spacing-4) var(--spacing-3);
  font-size: var(--text-sm);
  font-weight: var(--font-semibold);
  color: var(--text-secondary);
  border-bottom: 1px solid var(--border);
}

.videos-table tbody td {
  padding: var(--spacing-4) var(--spacing-3);
  font-size: var(--text-sm);
  color: var(--text-primary);
  border-bottom: 1px solid var(--border-light);
}

.videos-table tbody tr:hover {
  background-color: var(--surface-hover);
}

.video-cell {
  display: flex;
  align-items: center;
  gap: var(--spacing-3);
}

.video-thumb {
  width: 60px;
  height: 40px;
  object-fit: cover;
  border-radius: var(--radius-md);
}

.video-title {
  max-width: 300px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.platform-badge {
  padding: var(--spacing-1) var(--spacing-3);
  border-radius: var(--radius-full);
  background-color: var(--primary-light);
  color: var(--primary);
  font-size: var(--text-xs);
  font-weight: var(--font-semibold);
  text-transform: capitalize;
}

/* Creators Grid */
.creators-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: var(--spacing-4);
}

.creator-card {
  display: flex;
  align-items: center;
  gap: var(--spacing-4);
  padding: var(--spacing-4);
  border: 1px solid var(--border);
  border-radius: var(--radius-lg);
  transition: all var(--transition-fast);
}

.creator-card:hover {
  background-color: var(--surface-hover);
  box-shadow: var(--shadow-sm);
}

.creator-avatar {
  width: 56px;
  height: 56px;
  border-radius: var(--radius-full);
  object-fit: cover;
}

.creator-info {
  flex: 1;
  min-width: 0;
}

.creator-name {
  font-size: var(--text-base);
  font-weight: var(--font-semibold);
  color: var(--text-primary);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.creator-handle {
  font-size: var(--text-sm);
  color: var(--text-secondary);
  margin-bottom: var(--spacing-1);
}

.creator-stats {
  font-size: var(--text-xs);
  color: var(--text-tertiary);
  display: flex;
  gap: var(--spacing-2);
  align-items: center;
}

/* Empty State */
.empty-state {
  padding: var(--spacing-12);
  text-align: center;
  color: var(--text-secondary);
  font-size: var(--text-base);
}

/* Mobile Responsive */
@media (max-width: 768px) {
  .client-dashboard {
    padding: var(--spacing-4);
  }

  .dashboard-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .header-filters {
    width: 100%;
    flex-direction: column;
  }

  .campaign-select {
    width: 100%;
  }

  .metrics-grid {
    grid-template-columns: 1fr;
    gap: var(--spacing-4);
  }

  .card {
    padding: var(--spacing-4);
  }

  .section-header {
    flex-direction: column;
    align-items: flex-start;
    gap: var(--spacing-3);
  }

  .videos-table {
    display: block;
  }

  .videos-table table,
  .videos-table thead,
  .videos-table tbody,
  .videos-table tr,
  .videos-table td {
    display: block;
    width: 100%;
  }

  .videos-table thead {
    display: none;
  }

  .videos-table tbody tr {
    margin-bottom: var(--spacing-4);
    border: 1px solid var(--border);
    border-radius: var(--radius-lg);
    padding: var(--spacing-3);
  }

  .creators-grid {
    grid-template-columns: 1fr;
  }
}

