.all-videos-page {
  padding: var(--spacing-8);
  max-width: 1600px;
  margin: 0 auto;
}

.page-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: var(--spacing-6);
}

.page-title {
  font-size: var(--text-3xl);
  font-weight: var(--font-bold);
  color: var(--text-primary);
}

.header-actions {
  display: flex;
  gap: var(--spacing-3);
}

/* Table Cell Styles */
.creator-cell {
  display: flex;
  align-items: center;
  gap: var(--spacing-3);
}

.creator-avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
}

.platform-badge {
  display: inline-block;
  padding: var(--spacing-1) var(--spacing-3);
  background: var(--surface-hover);
  border-radius: var(--radius-md);
  font-size: var(--text-xs);
  font-weight: var(--font-medium);
  text-transform: capitalize;
}

.platform-badge.tiktok { background: #FEE2E2; color: #991B1B; }
.platform-badge.instagram { background: #FCE7F3; color: #831843; }
.platform-badge.youtube { background: #FEE2E2; color: #991B1B; }
.platform-badge.facebook { background: #DBEAFE; color: #1E3A8A; }
.platform-badge.snapchat { background: #FEF3C7; color: #78350F; }
.platform-badge.sora { background: #E0E7FF; color: #3730A3; }

.video-thumbnail {
  width: 80px;
  height: 45px;
  object-fit: cover;
  border-radius: var(--radius-md);
}

.video-title {
  max-width: 300px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: var(--z-modal);
  animation: fadeIn 0.2s;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.modal {
  background: var(--surface);
  border-radius: var(--radius-xl);
  width: 90%;
  max-width: 500px;
  box-shadow: var(--shadow-xl);
  animation: slideUp 0.3s;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.modal-header {
  padding: var(--spacing-6);
  border-bottom: 1px solid var(--border);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.modal-header h2 {
  font-size: var(--text-xl);
  font-weight: var(--font-semibold);
  color: var(--text-primary);
}

.modal-close {
  width: 32px;
  height: 32px;
  border-radius: var(--radius-md);
  background: transparent;
  color: var(--text-secondary);
  font-size: 20px;
  cursor: pointer;
  transition: all var(--transition-fast);
}

.modal-close:hover {
  background: var(--surface-hover);
  color: var(--text-primary);
}

.modal-body {
  padding: var(--spacing-6);
}

.input-label {
  display: block;
  font-size: var(--text-sm);
  font-weight: var(--font-medium);
  color: var(--text-primary);
  margin-bottom: var(--spacing-2);
}

.input {
  width: 100%;
  padding: var(--spacing-3) var(--spacing-4);
  background: var(--surface);
  border: 1px solid var(--border);
  border-radius: var(--radius-lg);
  font-size: var(--text-sm);
  color: var(--text-primary);
  transition: all var(--transition-fast);
}

.input:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 3px var(--primary-light);
}

.input-hint {
  margin-top: var(--spacing-2);
  font-size: var(--text-xs);
  color: var(--text-secondary);
}

.modal-footer {
  padding: var(--spacing-6);
  border-top: 1px solid var(--border);
  display: flex;
  gap: var(--spacing-3);
  justify-content: flex-end;
}

@media (max-width: 768px) {
  .all-videos-page {
    padding: var(--spacing-4);
  }

  .page-header {
    flex-direction: column;
    align-items: stretch;
    gap: var(--spacing-3);
  }

  .header-actions {
    flex-direction: column;
  }

  .modal {
    width: 95%;
    max-height: 90vh;
    overflow-y: auto;
  }
}
